import React from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => (
    <nav className="navbar sticky-top navbar-expand-lg navbar-light bg-nav-custom">
        <div className="container">
            <a className="navbar-brand font-italic" href="/">An Antiracism Project</a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            {/* use NavLink to allow for activeClass to change depending on what page we are on */}
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                {/* left-align these components with mr-auto */}
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item">
                        <NavLink exact activeClassName="active" className="nav-link" to="/">Home</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink activeClassName="active" className="nav-link" to="/fullList">Full Data Table</NavLink>
                    </li>
                </ul>
                {/* Make this stand out as another button */}
                <ul className="navbar-nav">
                    <li className="nav-item">
                        <NavLink className="btn btn-primary" to="/submitEntry">Submit an Entry</NavLink>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
)

export default Navigation;