import React from "react";

//static page to hold airTable form
class submitEntry extends React.Component {
    render() {
        return (
            <div className="container mt-5">
                <div className="row">
                    <h1>Contribute to the community</h1>
                    <p>This project was built by a community with the hopes of bringing together a better community. If you have suggestions, we'd love to hear from you!</p>
                </div>
                <div className="row">
                    <script src="https://static.airtable.com/js/embed/embed_snippet_v1.js"></script><iframe class="airtable-embed airtable-dynamic-height airTable-form-view" src="https://airtable.com/embed/shr0Mly7UD0cne35m?backgroundColor=blue" frameborder="0" onmousewheel=""></iframe>
                </div>
            </div>
        )
    }
}


export default submitEntry;