import React, { Component } from 'react';
import BadgesOutput from './BadgesOutput';
import Filters from './ATFilters';
// import { render } from '@testing-library/react'; used only in testing environment

//main filtering page
class Home extends Component {

    constructor(props) {
        //initial constructor to take in input from airtable API...starts as blank
        super(props);

        //bind masterFilterListner so it can access state and props
        this.masterFilterListner = this.masterFilterListner.bind(this);

        //set state to include booleans for all badges
        this.state = {
            filteredCards: [],
            passingBadges: {
                TypeOfActionCat: {
                    "Action Guide": false,
                    Article: false,
                    Book: false,
                    "Legal Services": false,
                    Movie: false,
                    "News Source": false,
                    Podcast: false,
                    "Short Video": false,
                    "Support Businesses": false,
                    "TV series": false,
                    "Donation": false,
                    "academic Paper": false,
                    "Structured Learning": false,
                },
                IntersectionalityCat: {
                    "LGBTQ+": false,
                    "Feminist": false,
                    "Indigenous Rights": false,
                    "South Asian": false
                },
                SubtopicCat: {
                    Healthcare: false,
                    "Criminal Justice": false,
                    Immigration: false,
                    "Voting Rights": false,
                    "Economic Inequality": false,
                    "Environmental Justice": false,
                    "Labor Rights": false,
                    "Reproductive Rights": false,
                    "Sexual Assault and Rape": false,
                    "Mental Health": false,
                    "Education": false,
                    "Bail Funds": false,
                    Policing: false
                },
                RecAudienceCat: {
                    Kids: false,
                    "Young Adults": false,
                    Teens: false,
                    Parents: false
                },
                StarterCat: false
            }
        };
    }

    //Store all cards for future callbacks and toggling
    fullAirtableCards = [];

    //fetch all records from airtable DB using read-only API-key
    componentDidMount() {
        fetch('https://api.airtable.com/v0/appAPRM3pAfHUj3LP/Raw%20Database?api_key=keycv2X09Q0ghqQfu')
            .then((resp) => resp.json())
            .then(data => {
                this.fullAirtableCards = data.records;
                this.setState({ filteredCards: data.records });
            }).catch(err => {
                alert("There was an error pulling from the airtable DB")
            })
    }

    //function to edit boolean values on click of any badges
    masterFilterListner = (e, cat) => {
        console.log("Filter clicked", e.target.dataset.name)
        const name = e.target.dataset.name;
        this.setState(prevState => ({
            passingBadges: {
                ...prevState.passingBadges,
                [cat]: {
                    ...prevState.passingBadges[cat],
                    [name]: !prevState.passingBadges[cat][name]
                },
                ["StarterCat"]: false
            }
        }));
    };

    //function that combines all boolean values to use on filtering
    combinedFilters = () => {
        const activatedBadges = {
            TypeOfAction: [],
            Intersectionality: [],
            Subtopic: [],
            RecAudience: [],
            Starter: false
        };

        const { TypeOfActionCat, IntersectionalityCat, SubtopicCat, RecAudienceCat } = this.state.passingBadges;

        //pull out the keys, which are the actual badge names, for anything that is true
        for (let key in TypeOfActionCat) {
            if (TypeOfActionCat[key]) {
                activatedBadges.TypeOfAction.push(key);
            }
        };
        for (let key in IntersectionalityCat) {
            if (IntersectionalityCat[key]) {
                activatedBadges.Intersectionality.push(key);
            }
        };
        for (let key in SubtopicCat) {
            if (SubtopicCat[key]) {
                activatedBadges.Subtopic.push(key);
            }
        };
        for (let key in RecAudienceCat) {
            if (RecAudienceCat[key]) {
                activatedBadges.RecAudience.push(key);
            }
        };

        activatedBadges.Starter = this.state.passingBadges.StarterCat;

        return activatedBadges;

    }

    multiPropsFilter = (cards, filters) => {

        //special case if starter button is selected
        if (filters["Starter"]) {
            return cards.filter(card => (card.fields["Starter"]))
        }
        //normal iteration through different selector tags
        const filterKeys = Object.keys(filters);
        return cards.filter(card => {
            return filterKeys.every(key => {
                if (!filters[key].length) return true;

                // Loops again if card[key] is an array (as is the case for almost all criteria)
                if (Array.isArray(card.fields[key])) {
                    return card.fields[key].some(keyEle => filters[key].includes(keyEle));
                }
                return filters[key].includes(card.fields[key]);
            });
        });
    };

    resetFilters() {

        const resetBadges = { ...this.state.passingBadges }

        //set temp variable to all false
        Object.keys(resetBadges).forEach(cat => {

            //starter is the only non-array cat, so avoid the double iteration
            if (cat === "StarterCat") {
                resetBadges[cat] = false;

                //iterate to nested properties and set to false
            } else {
                Object.keys(resetBadges[cat]).forEach(i => resetBadges[cat][i] = false)
            }
        })

        this.setState(
            { passingBadges: resetBadges }
        );
    }

    starterSelect() {

        //remove any filters first
        this.resetFilters()

        //set up copy variable
        const resetBadges = { ...this.state.passingBadges }

        resetBadges.StarterCat = true;

        this.setState(
            { passingBadges: resetBadges }
        );

    }

    render() {
        //prevents rendering too early
        if (this.state.filteredCards === null) return null;

        return (

            //begin filter section
            <div className="container mt-5">
                <h1>Build your plan <small className="text-muted vsmall">Click on any tag to filter, use our starter kit, or just scroll</small></h1>

                <div className="cotainer mb-3">

                    <Filters airtableData={this.fullAirtableCards} passingBadges={this.state.passingBadges} clickAction={this.masterFilterListner} />

                    <div className="btn-toolbar row">
                        <div className="offset-4">
                            <button type="button" className="btn btn-primary mx-1" onClick={() => this.starterSelect()} id="#">Show me some starters!</button>

                            <button type="button" className="btn btn-secondary mx-1" onClick={() => this.resetFilters()} id="#">Reset</button>
                        </div>
                    </div>
                </div>

                {/* begin main section returning cards, using the filtering function to pass all the filters to the full list of cards every time */}
                <FadeInDiv>
                    <h1 className="mt-5">Resources to educate and act <small className="text-muted vsmall">Click on any image to go to the site</small></h1>
                </FadeInDiv>

                <div className="row row-cols-1 row-cols-lg-4 card-matrix mt-4">
                    {this.multiPropsFilter(this.fullAirtableCards, this.combinedFilters()).map(resource => <ResourceCard {...resource.fields} />)}
                </div>

            </div>
        );
    }
}

export default Home;

//add fading effects to any div on view
function FadeInDiv(props) {

    //set variables to capture visibility state
    const [isVisible, setVisible] = React.useState(false);
    const domRef = React.useRef(null);

    //instantize new observer for each element and trigger it when there is some change to the dom
    React.useEffect(() => {
        const observer = new IntersectionObserver(entries => {
            entries.forEach(entry => setVisible(entry.isIntersecting));
        });

        //observe the change in the dom
        observer.observe(domRef.current);
        //unmount observer after the change is dom
        return () => observer.unobserve(domRef.current);
    });

    return (
        <div ref={domRef} className={"col mb-4 fader " + (isVisible ? "is-visible" : "")}>{props.children}</div>
    );
}

const ResourceCard = ({ Title, TypeOfAction, Link, ShortDescription, Intersectionality, RecAudience, Starter, Subtopic, ImageURL, id }) => (
    <FadeInDiv key={id}>

        {/* Each type of action has been coded to a particular card color...grab the color of the first type of action that comes up*/}
        <div className={"card h-100 card-highlights " + (TypeOfAction[0].toLowerCase().split(" ").join("-").replace("+", "plus"))}>
            <a href={Link} target="_blank">
                <img className="card-img-top" src={ImageURL} alt={"Website preview for " + { Title }} />
            </a>

            <div className="card-body">
                <div className="height-setter">
                    <h5 className="card-title">{Title + " "}
                        {/* Apply starter badge if starter is checked */}
                        <small className={"badge badge-dark " + (Starter ? "" : "d-none")}> STARTER</small>
                    </h5>
                </div>
                {/* Collapsable version (show only if text is long enough and screen size is big enough) */}
                <div className={"descriptionText d-none " + ((ShortDescription.length < 106) ? "" : "d-lg-block d-xl-block")}>
                    <p className="card-text collapse" id={"descriptionContent" + id} aria-expanded="false">{ShortDescription}</p>
                    <a href={"#descriptionContent" + id} data-toggle="collapse" className="collapsed" aria-expanded="false" aria-controls="descriptionContent">
                        <span className="readMore">+Show More</span>
                        <span className="readLess">-Show Less</span>
                    </a>
                </div>

                {/* Full version (show if text is short and/or screen size is iPad  or lower) */}
                <div className={"descriptionText " + ((ShortDescription.length < 106) ? "d-block" : "d-block d-lg-none")}>
                    <p className="card-text">{ShortDescription}</p>
                </div>


                {/* <div className="pill-matrix pt-3">
        <div class="border-top pt-1 border-bottom">
          <p class="font-weight-bold">Intersectionality: {"  "} 
          <BadgesOutput BadgesData = {Intersectionality} LegendCheck = {false} />
          </p>
        </div>
        <div class="pt-1 border-bottom">
          <p class="font-weight-bold">Subtopic: {"  "} 
          <BadgesOutput BadgesData = {Subtopic} LegendCheck = {false} />
          </p>
        </div>
        <div class="pt-1 border-bottom">
          <p class="font-weight-bold">Action Type: {"  "} 
          <BadgesOutput BadgesData = {TypeOfAction} LegendCheck = {false} />
          </p>
        </div>
        <div class="pt-1 border-bottom">
          <p class="font-weight-bold">Rec. Audience: {"  "} 
          <BadgesOutput BadgesData = {RecAudience} LegendCheck = {false} />
          </p>
        </div>
      </div> */}


                <div className="row p-3 text-center pill-matrix">
                    <div className="col-sm-6 pb-3 border-right border-bottom">
                        {/* Annoying fix due to the length of the word intersectionality */}
                        <span className="d-none d-lg-block">Intersection-<br />ality<br /></span>
                        <span className="d-block d-lg-none">Intersectionality<br /></span>
                        {/*loop through string array to find all of the badges for each section, and add each badge with the right coloring */}
                        <BadgesOutput BadgesData={Intersectionality} LegendCheck={false} />
                    </div>
                    <div className="col-sm-6 pb-3 border-bottom">
                        <p>Subtopics</p>
                        <BadgesOutput BadgesData={Subtopic} LegendCheck={false} />
                    </div>
                    <div className="col-sm-6 border-right pr-3">
                        <p>Action Types</p>
                        <BadgesOutput BadgesData={TypeOfAction} LegendCheck={false} />
                    </div>
                    <div className="col-sm-6">
                        <p>Rec. Audience</p>
                        <BadgesOutput BadgesData={RecAudience} LegendCheck={false} />
                    </div>
                </div>
            </div>
        </div>
    </FadeInDiv>
);
